#footer .footer-bottom {
  padding-top: 30px;
  padding-bottom: 30px;
  color: #000;
  background: #fff;
}

#footer .credits {
  float: right;
  font-size: 13px;
}

#footer .credits a {
  transition: 0.3s;
}

@media (max-width: 768px) {
  #footer .footer-bottom {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  #footer .copyright,
  #footer .credits {
    text-align: center;
    float: none;
  }

  #footer .credits {
    padding-top: 4px;
  }
}

#services .icon {
  margin-bottom: 30px;
}

section#skills p {
  color: white;
}
#products .icon img {
  height: 231px !important;
  object-fit: cover !important;
  border-radius: 20px;
}

#products .img-fluid {
  max-width: 100%;
  height: 230px;
  object-fit: cover;
  border-radius: 20px;
}
#cta img {
  width: 200px;
}
#footer .footer-contact {
  text-align: center;
}

.navbar a:hover {
  color: black !important;
  cursor: pointer;
}

@font-face {
  font-display: block;
  font-family: "bootstrap-icons";
  src: url("./fonts/bootstrap-icons.woff2?8d200481aa7f02a2d63a331fc782cfaf")
      format("woff2"),
    url("./fonts/bootstrap-icons.woff?8d200481aa7f02a2d63a331fc782cfaf")
      format("woff");
}

.bi::before,
[class^="bi-"]::before,
[class*=" bi-"]::before {
  display: inline-block;
  font-family: bootstrap-icons !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: -0.125em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.bi-list::before {
  content: "\f479";
}
.bi-x::before {
  content: "\f62a";
}

.bi-play-circle::before {
  content: "\f4f3";
}
.bi-arrow-up-short::before {
  content: "\f145";
}
